*, :before, :after {
  box-sizing: border-box;
}

ul, ol {
  padding: 0;
}

body, h1, h2, h3, h4, p, ul, ol, li, figure, figcaption {
  margin: 0;
}

body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizespeed;
  line-height: 1.5;
}

ul, ol {
  list-style: none;
}

a {
  -webkit-text-decoration-skip-ink: auto;
  text-decoration-skip-ink: auto;
}

img {
  max-width: 100%;
  display: block;
}

input, button, textarea, select {
  font: inherit;
  padding: 0;
}

.header {
  width: 100%;
  height: 50px;
  background-color: #191970;
  justify-content: center;
  align-items: center;
  display: flex;
}

.search-form {
  width: 500px;
  height: 40px;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  display: flex;
}

.form-input {
  width: 300px;
  height: 40px;
  border: 0;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  padding-left: 20px;
  font-family: Explora, cursive;
  font-size: 28px;
  font-weight: 500;
  transition: transform .5s cubic-bezier(.075, .82, .165, 1);
}

.form-input::-ms-input-placeholder {
  font-family: Explora, cursive;
  font-size: 28px;
  font-weight: 500;
}

.form-input::placeholder {
  font-family: Explora, cursive;
  font-size: 28px;
  font-weight: 500;
}

.form-input:hover, .form-input:focus {
  outline: none;
  transform: scale(1.1);
}

.button {
  height: 40px;
  width: 150px;
  cursor: pointer;
  background-color: #e0ffff;
  border: 0;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  font-family: Explora, cursive;
  font-size: 28px;
  font-weight: 500;
  transition: transform .5s cubic-bezier(.075, .82, .165, 1);
  display: flex;
}

.button:hover, .button:focus {
  border-radius: 10px;
  outline: none;
  transform: scale(1.1);
}

.form-input:hover + button, .form-input:focus + button {
  outline: none;
  transform: scale(1.1);
}

.gallery {
  grid-template-areas: "cards"
                       "button";
  display: grid;
}

.gallery .button {
  grid-area: button;
  justify-self: center;
}

.gallery__wrapper {
  height: 100%;
  width: 100%;
  grid-area: cards;
  grid-template-columns: repeat(5, 1fr);
  grid-auto-rows: auto;
  align-items: center;
  justify-items: center;
  gap: 10px;
  padding: 20px;
  display: grid;
}

